<template>
  <div class="scratchFun">
    <img class="bgImg" src="../assets/scratchfun/1.png">
    <!-- 奖品规则按钮 -->
    <div class="guideBtn" @click="showRules">奖品规则</div>
    <!-- 奖品规则详情 -->
    <div class="rules" v-show="rulesShow">
      <div class="container">
        <div class="title"><img src="../assets/scratchfun/4.png"></div>
        <p>每人每日有三次抽奖机会，<br>点击刮刮卡即可开启，开启后有机会中奖</p>
        <ul>
          <li v-for="item in prizeList">
            <div class="introLeft">
              <img src="../assets/memorycard/2.png">
              <div class="name">{{item.alias}}</div>
            </div>
            <div class="introRight">
              <span>{{item.name}} 中奖概率{{item.probability}}%</span>
            </div>
          </li>
        </ul>
        <div class="closeBtn" @click="closeGuide"><img src="../assets/memorycard/6.png"></div>
      </div>
    </div>
    <!-- 刮刮乐部分 -->
    <div class="scratchContaniner" ref="scratchContainer">
      <div class="scratch" ref="scratch">
        <div class="result">{{num}}元旅行红包</div>
        <canvas></canvas>
      </div>
      <p class="tips">今日还有<span>{{count}}</span>次刮奖机会</p>
    </div>
    <div class="gameResult" v-show="resultShow">
      <img src="../assets/scratchfun/5.png" v-show="playWin">
      <img src="../assets/scratchfun/6.png" v-show="!playWin">
      <div class="btn useBtn"
        v-show="playWin"
        @click="readIntros">查看使用说明</div>
      <div class="btn playBtn"
        v-show="count > 0"
        @click="playAgain">再玩一次</div>
    </div>
  </div>
</template>
<script>
import _ from 'lodash'
export default {
  data () {
    return {
      prizeList: [{
        name: 'dyson吸尘器',
        probability: 10,
        pic: '',
        alias: '奖品一'
      },
      {
        name: '苹果',
        probability: 20,
        pic: '',
        alias: '奖品二'
      },
      {
        name: '橘子',
        probability: 60,
        pic: '',
        alias: '奖品三'
      }],
      rulesShow: false,
      resultShow: false,
      playWin: true,
      canvas: null,
      ctx: null,
      ismousedown: false,
      num: 0,
      count: 3
    }
  },
  mounted () {
    this.num = _.random(0, 20)

    let w = this.$refs.scratch.clientWidth
    let h = this.$refs.scratch.clientHeight

    this.canvas = document.querySelector('canvas')
    this.canvas.style.position = 'absolute'
    this.canvas.style.left = '0'
    this.canvas.style.top = '0'
    // 这里很关键，canvas自带两个属性width、height,我理解为画布的分辨率，跟style中的width、height意义不同。
    // 最好设置成跟画布在页面中的实际大小一样
    // 不然canvas中的坐标跟鼠标的坐标无法匹配
    this.canvas.width = w
    this.canvas.height = h
    this.ctx = this.canvas.getContext('2d')
    // this.ctx.globalCompositeOperation = 'destination-out'
    this.initCanvas()

    // 移动端的处理
    this.canvas.addEventListener('touchstart', this.eventDown)
    this.canvas.addEventListener('touchend', this.eventUp)
    this.canvas.addEventListener('touchmove', this.eventMove)
    // PC端的处理
    this.canvas.addEventListener('mousemove', this.eventMove)
    this.canvas.addEventListener('mousedown', this.eventDown)
    this.canvas.addEventListener('mouseup', this.eventUp)
  },
  methods: {
    // 鼠标按下 和 触摸开始
    eventDown: function (e) {
      this.ismousedown = true
    },
    // 鼠标抬起 和 触摸结束
    eventUp: function (e) {
      // 得到canvas的全部数据
      let imageData = this.ctx.getImageData(0, 0, this.canvas.width, this.canvas.height)
      let j = 0
      for (let i = 3; i < imageData.data.length; i += 4) {
        if (imageData.data[i] === 0) {
          j++
        }
      }
      // 当被刮开的区域等于一半时，则可以开始处理结果
      if (j >= imageData.data.length / 8) {
        let that = this
        window.setTimeout(function () {
          that.resultShow = true
          that.count --
        }, 200)
      }
      this.ismousedown = false
    },
    // 鼠标移动 和 触摸移动
    eventMove: function (e) {
      if (this.ismousedown) {
        if (e.changedTouches) {
          e = e.changedTouches[e.changedTouches.length - 1]
        }
        let topX = this.$refs.scratch.offsetLeft
        let topY = this.$refs.scratch.offsetTop + this.$refs.scratchContainer.offsetTop
        let oX = this.canvas.offsetLeft + topX
        let oY = this.canvas.offsetTop + topY
        let x = (e.clientX || e.pageX) - oX || 0
        let y = (e.clientY || e.pageY) - oY || 0
        this.ctx.beginPath()
        this.ctx.arc(x, y, 15, 0, Math.PI * 2, true)

        this.canvas.style.display = 'none'
        this.canvas.style.display = 'inherit'

        this.ctx.fill()
      }
    },
    // 初始化画布
    initCanvas: function () {
      // 使用不同的 globalCompositeOperation 值绘制矩形
      // source-over 默认值，他表示绘制的图形将画在现有画布之上
      this.ctx.globalCompositeOperation = 'source-over'
      // 画布背景色
      this.ctx.fillStyle = '#d8d8d8'
      this.ctx.fillRect(0, 0, this.canvas.clientWidth, this.canvas.clientHeight)
      this.ctx.fill()
      // 画布内文字
      this.ctx.font = '20px PingFangSC-Regular'
      this.ctx.textAlign = 'center'
      this.ctx.fillStyle = '#1E1F4B'
      this.ctx.fillText('开始刮好礼', this.canvas.width / 2, this.canvas.height / 1.5)
      // destination-out 在与目标不重叠的区域上绘制源，其他部分都变成透明的。
      this.ctx.globalCompositeOperation = 'destination-out'
    },
    // 点击奖品规则
    showRules: function () { this.rulesShow = true },
    closeGuide: function () { this.rulesShow = false },
    // 查看使用说明
    readIntros: function () {},
    // 再玩一次
    playAgain: function () {
      this.resultShow = false
      this.num = _.random(0, 20)
      this.initCanvas()
    }
  }
}
</script>
<style>
.scratchFun .scratchContaniner{
  width: 750px;
  height: 303px;
  background-image: url('./src/assets/scratchfun/3.png');
  background-size: 100% 100%;
  position: fixed;
  left: 0;
  bottom: 0;
}
.scratchFun .scratchContaniner .scratch{
  width: 300px;
  height: 80px;
  margin: 50px auto 0;
  position: relative;
}
.scratchFun .scratchContaniner .tips{
  font-family: PingFangSC-Regular;
  color: #1E1F4B;
  font-size: 20px;
  line-height: 40px;
  letter-spacing: 2px;
  text-align: center;
}
.scratchFun .scratchContaniner .tips span{
  font-size: 30px;
  line-height: 28px;
  color: #F05151;
}
.scratchFun .scratch .result{
  width: 100%;
  height: 100%;
  font-family: PingFangSC-Medium;
  font-size: 32px;
  line-height: 88px;
  color: #F05151;
  letter-spacing: 4px;
  text-align: center;
}
.scratchFun,.scratchFun *{
  line-height: 0;
  padding: 0;
  margin: 0;
}
.scratchFun{
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: fixed;
  left: 0;
  top: 0;
}
.scratchFun img{ width: 100%; }
.scratchFun .guideBtn{
  box-sizing: border-box;
  height: 48px;
  padding: 7px 14px;
  border-radius: 24px 0 0 24px;
  background-color: #000c;
  font-family: PingFangSC-Regular;
  font-size: 24px;
  line-height: 34px;
  color: #FFFFFF;
  text-align: center;
  position: absolute;
  right: 0;
  top: 160px;
}
.scratchFun .rules{
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  position: fixed;
  left: 0;
  top: 0;
  z-index: 100;
}
.scratchFun .rules .container{
  width: 686px;
  height: 882px;
  border-radius: 20px;
  background-image: url('./src/assets/scratchfun/2.png');
  background-size: 100% 100%;
  position: fixed;
  left: 50%;
  top: 50%;
  margin-left: -343px;
  margin-top: -441px;
}
.scratchFun .rules .container .title{
  width: 100%;
  height: 100px;
  border-bottom: 1px solid #e7e7e780;
  display: flex;
  align-items: center;
  justify-content: center;
}
.scratchFun .rules .container .title img{
  width: 140px;
  height: 36px;
}
.scratchFun .rules .container p{
  padding: 18px 0px 40px;
  font-family: PingFangSC-Medium;
  color: #fff;
  font-size: 28px;
  line-height: 36px;
  text-align: center;
}
.scratchFun .rules .container ul{
  width: 654px;
  margin: 0 auto;
}
.scratchFun .rules .container ul li{
  box-sizing: border-box;
  display: flex;
  width: 654px;
  height: 185px;
  padding: 22px 24px;
  border-radius: 10px;
  margin-bottom: 16px;
  background-color: #fff;
  position: relative;
}
.scratchFun .rules .container ul li .introLeft{
  width: 142px;
  height: 142px;
  border-radius: 10px;
  margin-right: 30px;
  position: relative;
}
.scratchFun .rules .container ul li .introLeft .name{
  width: 100%;
  height: 40px;
  border-radius: 0px 0px 10px 10px;
  background: rgba(0, 0, 0, 0.35);
  font-family: PingFangSC-Medium;
  color: #fff;
  font-size: 28px;
  line-height: 40px;
  text-align: center;
  position: absolute;
  left: 0;
  bottom: 0;
}
.scratchFun .rules .container ul li .introRight{
  width: 434px;
}
.scratchFun .rules .container ul li .introRight span{
  font-family: PingFangSC-Medium;
  color: #424242;
  font-size: 28px;
  line-height: 40px;
  text-align: center;
}
.scratchFun .closeBtn{
  width: 48px;
  height: 48px;
  position: absolute;
  right: 0;
  top: -63px;
}
.scratchFun .gameResult{
  width: 100%;
  height: 100%;
  background-image: linear-gradient(-180deg, #FFFFFF 65%, #FDEFE7 100%);
  position: fixed;
  left: 0;
  top: 0;
  text-align: center;
}
.scratchFun .gameResult img{
  width: 60%;
  margin: 50px 0 250px 0;
}
.scratchFun .gameResult .btn{
  box-sizing: border-box;
  width: 344px;
  height: 94px;
  border-radius: 47px;
  text-align: center;
  letter-spacing: 4px;
}
.scratchFun .gameResult .useBtn{
  border: 2px solid #BDBDBD;
  margin: 0 auto 55px;
  font-family: PingFangSC-Medium;
  color: #424242;
  font-size: 32px;
  line-height: 90px;
}
.scratchFun .gameResult .playBtn{
  margin: 0 auto;
  background-image: linear-gradient(-172deg, #FFD06D 0%, #FA5343 100%);
  box-shadow: 0 3px 30px 8px rgba(250,86,68,0.40);
  font-family: PingFangSC-Medium;
  color: #fff;
  font-size: 32px;
  line-height: 94px;
}
</style>



